@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS EN LA BARRA LATERAL DE DASHBOARD

.sidebar{
    background-color: v.$indigo;
    padding: 2rem;

    h2{
        color: v.$blanco;
        margin: 0;
        text-align: center;
    }

    .sidebar-nav{
        margin-top: 5rem;

        a{
            color: v.$blanco;
            display: block;
            margin-bottom: 1.5rem;
            padding: .7rem;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 1.5rem;
            transition-property: background-color;
            transition-duration: 300ms;

            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }
        }
    }
}